.color-card {
    width: 270px;
    height: 280px;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    position: relative;
    
    overflow: hidden;
  }
  
  .color-card:hover {
    transform: scale(1.05); /* Subtle zoom effect */
  }
  
  .color-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 0.3s ease; 
  }
  
  .color-themes {
    display: flex;
    flex-direction: column; 
    width: 100%;
    height: 100%;
  }
  
  .color-segment {
    flex: 1;
    transition: background-color 0.3s ease;
   
  }

  .color-button {
    width: 100%;
    height: 100%;
    background-color: transparent;
    font-size: 14px;
    font-family: 'Arial', sans-serif;
    text-align: center;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .color-button:hover {
    background-color: rgba(255, 255, 255, 0.2); /* Subtle hover effect */
  }
  
